import Table from "../../ui/Table";
import useAppStore from "components/context/app/Hooks";
import { DashboardDiagnostics, Feature } from "types";
import NotLicensed from "components/pages/notLicensed";
import { useParams } from "react-router-dom";
import { useQuery } from "react-query3";

const DashboardEndpoints = () => {
  const { id } = useParams();
  const { licensed } = useAppStore();
  const { data: endpoints } = useQuery<any>(`/dashboard/${id}/diagnostics`, {
    enabled: !!id && licensed(Feature.Dashboard),
    select: (data: DashboardDiagnostics) => {
      return data?.endpoints;
    },
  });

  const columns = [
    {
      title: "Id",
      key: "id",
      dataIndex: "id",
    },
    {
      title: "Average Execution Time",
      key: "averageExecutionTime",
      dataIndex: "averageExecutionTime",
      render: (text) => `${text}ms`,
    },
  ];

  if (!licensed(Feature.Dashboard)) {
    return (
      <NotLicensed title="Endpoints are only available for licensed instances." />
    );
  }

  return <Table data={endpoints} columns={columns} />;
};

export default DashboardEndpoints;
